<template>
  <div @click="log_data">
    <div class="elements">
      <div class="element" v-for="(element, eid) in charactor.element_application" :key="eid">
        <img :src="image_path('ELEMENT', element)" width="100%" height="100%">
      </div>
    </div>
    <div :class="(charactor.is_alive ? 'charactor ' : 'charactor-died ') + selectClass">
      <img class="charactor-image" :src="image_path('CHARACTOR', charactor.name, charactor.desc)" :alt="$t('CHARACTOR/' + charactor.name + (charactor.desc.length > 0 ? '_' : '') + charactor.desc)">
      <div class="charactor-hp">
        <div>
          <img :src="image_path('ICON', 'HP')" width="100%" heright="100%"/>
          <div class="hp-span-div">
            <span :style="charactor.hp > 9 ? 'padding-right: 0.2vw;' : ''">{{ charactor.hp }}</span>
          </div>
        </div>
      </div>
      <div class="charactor-charge-back">
        <div v-for="i in charactor.max_charge" :key="i">
          <img :src="image_path('ICON', 'CHARGE_BACK')" width="100%" height="100%" />
        </div>
      </div>
      <div class="charactor-charge-front">
        <div v-for="i in charactor.charge" :key="i">
          <img :src="image_path('ICON', 'CHARGE_FRONT')" width="100%" height="100%" />
        </div>
      </div>
      <div v-if="charactor.weapon" @click="log_object(charactor.weapon)" @mouseover="showDetails(charactor.weapon)" @mouseout="hideDetails()" class="charactor-weapon">
        <img :src="image_path('ICON', 'EQUIP_WEAPON')" width="100%" height="100%" />
      </div>
      <div v-if="charactor.artifact" @click="log_object(charactor.artifact)" @mouseover="showDetails(charactor.artifact)" @mouseout="hideDetails()" class="charactor-artifact">
        <img :src="image_path('ICON', 'EQUIP_ARTIFACT')" width="100%" height="100%" />
      </div>
      <div v-if="charactor.talent" @click="log_object(charactor.talent)" @mouseover="showDetails(charactor.talent)" @mouseout="hideDetails()" class="charactor-talent">
        <img :src="image_path('ICON', 'EQUIP_TALENT')" width="100%" height="100%" />
      </div>
      <div v-if="showDetailsFlag" class="charactor-details" :style="detailDivStyle(detailData)">
        <div class="p-div" :style="'width: ' + detailTextWidth + '%'">
          <h4>{{ $t(detailData.type + '/' + $store.getters.getNameWithDesc(detailData)) }}</h4>
          <p style="color: #555; font-size: 0.65vw;">{{ $t('Version: ') }}{{ detailData.version }}</p>
          <p>{{ $t(detailData.type + '/' + $store.getters.getNameWithDesc(detailData) + '/' + detailData.version) }}</p>
          <!-- <p>Usage: {{ detailData.usage }}</p> -->
        </div>
        <div class="detail-img-div" v-if="detailData.img_name" :style="'width: ' + detailImgWidth + '%'">
          <img :src="image_path(detailData.type, detailData.img_name, detailData.desc)" width="100%" height="100%" />
        </div>
      </div>
      <div class="damage-notify-div" v-if="damage_notify.length > 0">
        <p v-for="(notify, nid) in damage_notify" :key="nid" :style="{ color: notify.color, 'font-size': notify.font_size }">{{ notify.damage }}</p>
      </div>
      <div class="charactor-status-div">
        <div v-for="(status, sid) in charactor.status" :key="sid" @click="log_object(status)" @mouseover="showDetails(status, false)" @mouseout="hideDetails()" >
          <img :src="status_path(status)" width="100%" height="100%" />
          <div class="usage-span-div">
            <span v-if="status.usage && status.usage > 0">{{ status.usage }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Charactor',
  props: {
    charactor: {
      type: Object,
      required: true
    },
    detailTextWidth: {
      type: Number,
      default: 175
    },
    selectClass: {
      type: String,
      default: 'select-none'
    }
  },
  data() {
    return {
      showDetailsFlag: false,
      detailData: null,
      detailImgWidth: 50,
      detailOffset: 15,
    }
  },
  methods: {
    log_data() {
      console.log('CHARACTOR', JSON.parse(JSON.stringify(this.charactor)));
      this.$store.commit('setSelectedObject', this.charactor)
    },
    log_object(obj) {
      console.log(obj.type, JSON.parse(JSON.stringify(obj)));
    },
    showDetails(data, img = true) {
      data = JSON.parse(JSON.stringify(data));
      this.showDetailsFlag = true;
      if (img) data.img_name = data.name;
      if (data.type == 'TALENT') {
        data.type = data.type + '_' + data.charactor_name;
      }
      this.detailData = data;
    },
    hideDetails() {
      this.showDetailsFlag = false;
    },
    status_path(status) {
      let name = status.name;
      let desc = status.desc;
      if (status.icon_type != 'OTHERS') {
        name = status.icon_type;
        desc = '';
      }
      return this.$store.getters.getImagePath({
        type: status.type,
        name: name,
        desc: desc
      })
    },
    image_path(type, name, desc) {
      return this.$store.getters.getImagePath({
        type: type,
        name: name,
        desc: desc
      })
    },
    detailDivStyle(detailData) {
      let text_width = this.detailTextWidth;
      let img_width = this.detailImgWidth;
      let offset = this.detailOffset;
      let width = 0;
      if (detailData.img_name) {
        width = text_width + img_width;
      }
      else {
        width = text_width;
      }
      let left = - width - offset;
      return 'width: ' + width + '%; left: ' + left + '%';
    }
  },
  computed: {
    damage_notify() {
      let notify = this.$store.state.damageNotify;
      if (notify && notify[this.charactor.position.player_idx]
          && notify[this.charactor.position.player_idx][this.charactor.position.charactor_idx]) {
        let res = [];
        for (let i = 0; i < notify[this.charactor.position.player_idx][this.charactor.position.charactor_idx].length; i++) {
          let item = notify[this.charactor.position.player_idx][this.charactor.position.charactor_idx][i];
          item = JSON.parse(JSON.stringify(item));
          if (item.type == 'HEAL') item.damage = '+' + item.damage;
          else item.damage = '-' + item.damage;
          let colors = {
            'PYRO': 'rgb(242, 121, 71)',
            'CRYO': 'rgb(111, 215, 225)',
            'HYDRO': 'rgb(95, 154, 227)',
            'ELECTRO': 'rgb(181, 123, 236)',
            'ANEMO': 'rgb(88, 220, 171)',
            'GEO': 'rgb(225, 169, 16)',
            'DENDRO': 'rgb(151, 200, 89)',
            'PHYSICAL': 'rgb(119, 106, 102)',
            'HEAL': 'rgb(80, 213, 103)',
            'PIERCING': 'rgb(119, 106, 102)',
          }
          item.color = colors[item.type];
          res.push(item);
        }
        let sizes = ['3vw', '3vw', '2.5vw', '2vw', '1.5vw', '1vw', '0.5vw'];
        let target_size = res.length;
        if (target_size > 6) target_size = 6;
        for (let i = 0; i < res.length; i++) {
          res[i].font_size = sizes[target_size];
        }
        return res;
      }
      return [];
    }
  }
}
</script>

<style scoped>
.elements {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 10.44%;
}

.element {
  height: 100%;
}

.charactor, .charactor-died {
  position: relative;
  width: 100%;
  height: 89.56%;
  /* top: 10.44%; */
  /* text-shadow: 2px 2px 0px  #fff, -2px -2px 0px  #fff, 2px -2px 0px  #fff, -2px 2px 0px  #fff; */
  color: black;
  font-weight: bolder;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  font-size: 2vw;
}

.charactor-died {
  border: 1px solid red;
  background-color: red;
}

.charactor-died > * {
  opacity: 0.8;
}

.charactor > div, .charactor-died > div {
  /* Add styles for all the div elements inside the charactor container */
  position: absolute;
}

.charactor-image {
  /* Add styles for the charactor image */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  font-size: 70%;
  -webkit-text-stroke-width: 0;
}

.charactor-name {
  /* Add styles for the charactor name div */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.charactor-hp {
  /* Add styles for the charactor hp div */
  position: absolute;
  top: -5%;
  left: -17%;
  height: 30%;
  width: 41.856%;
  opacity: 1;
}

.charactor-hp > div {
  position: absolute;
  font-size: 1.7vw;
  font-weight: bolder;
  text-align: center;
  width: 100%;
  height: 100%;
  /* -webkit-text-stroke-width: 1px; */
  -webkit-text-stroke-color: black;
  /* line-height: 130%; */
  color: white;
  /* z-index: 999; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.hp-span-div {
  position: absolute;
  left: 0;
  letter-spacing: -0.2vw;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.charactor-charge {
  /* Add styles for the charactor charge div */
  display: flex;
  flex-direction: column;
  width: 20%;
  height: 100%;
  left: 80%;
  align-items: end;
}

.charactor-charge-back {
  display: flex;
  flex-direction: column;
  width: 26.01%;
  height: 100%;
  left: 87%;
  opacity: 1;
}

.charactor-charge-back > div {
  height: 15.174%;
  width: 100%;
  margin-top: 0.25vw;
}

.charactor-charge-front > div > img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.charactor-charge-front {
  display: flex;
  flex-direction: column;
  width: 40.81%;
  height: 100%;
  left: 79.6%;
  opacity: 1;
}

.charactor-charge-front > div {
  /* height: 19.62%; */
  position: relative;
  height: 15.174%;
  width: 100%;
  /* margin-top: 15%; */
  margin-top: 0.25vw;
}

.charactor-charge-front > div > img {
  position: absolute;
  height: 129.31%;
  /* top: -14.655%; */
}

.charactor-weapon {
  /* Add styles for the charactor weapon div */
  top: 25%;
  left: -15%;
  height: 20%;
  width: 36.2%;
}

.charactor-artifact {
  /* Add styles for the charactor artifact div */
  top: 45%;
  left: -15%;
  height: 20%;
  width: 36.2%;
}

.charactor-talent {
  /* Add styles for the charactor talent div */
  top: 65%;
  left: -15%;
  height: 20%;
  width: 36.2%;
}

.charactor-status-div {
  /* Add styles for the charactor status div */
  display: flex;
  flex-direction: row;
  top: 88.34%;
  height: 11.66%;
  width: 100%;
}

.charactor-status-div > div {
  position: relative;
  /* width: 10%;
  height: 10%; */
  width: 20%;
  height: 100%;
}

.charactor-status-div img {
  position: absolute;
}

.charactor-details {
  position: absolute;
  top: 0;
  height: 100%;
  background-color: white;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: black;
  z-index: 999;
  font-size: 0.75vw;
  font-weight: normal;
  -webkit-text-stroke-width: 0;
  -webkit-text-stroke-color: black;
}

.charactor-details > .detail-img-div {
  width: 50%;
}

.charactor-details > .p-div {
  color: black;
  background-color: rgba(255, 255, 255, 0.5);
}

.p-div > * {
  margin-top: 0;
}

.p-div > h4 {
  margin: 0;
}

.usage-span-div {
  position: absolute;
  font-size: 0.5vw;
  font-weight: bolder;
  text-align: center;
  height: 50%;
  top: 50%;
  left: 50%;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  /* line-height: 130%; */
  color: white;
  /* z-index: 999; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.select-none, .select-disabled, .select-highlight, .select-selected {
  border-radius: 0.65vw;
}

.select-disabled {
  box-shadow: 0 0 0.25vw 0.25vw rgb(192, 192, 192);
  opacity: 0.2;
}

.select-highlight {
  box-shadow: 0 0 0.25vw 0.25vw rgb(255, 174, 0);
}

.select-selected {
  box-shadow: 0 0 0.25vw 0.25vw rgb(255, 81, 0);
}

.damage-notify-div {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.damage-notify-div > * {
  border-radius: 1000px;
  background-color: rgb(244, 239, 216);
  box-shadow: 0 0 0.25vw 0.25vw rgb(211, 179, 138);
  margin: 0.25vw;
  padding: 0.25vw;
  -webkit-text-stroke-width: 0.5px;
}

</style>



